export default defineComponent({
  name: 'Home',
  components: {TheWelcome},
  setup() {
    console.log("setup");
    //动态响应
    const ebooks = ref();
    // const ebookA = reactive({books:[]});
    const level1 =  ref();
    let categorys: any;




    const handleQueryCategory = () => {
      axios.get("/category/all").then((response) => {
        const data = response.data;
        if (data.success) {
          categorys = data.content;
          console.log("original array：", categorys);

          level1.value = [];
          level1.value = Tool.arrayToTree(categorys, 0);
          console.log("tree form：", level1.value);
        } else {
          message.error(data.message);
        }
      });
    };

    const isShowWelcome = ref(true);
    let categoryId2 = 0;

    const handleQueryEbook = () => {
      axios.get("/ebook/list", {
        params: {
          page: 1,
          size: 1000,
          categoryId2: categoryId2
        }
      }).then((response) => {
        //data: CommonResp
        const data = response.data;
        ebooks.value = data.content.list;
        // ebookA.books = data.content;
      });
    };

    const handleClick = (value: any) => {
      // console.log("menu click", value)
      if (value.key === 'welcome') {
        isShowWelcome.value = true;
      } else {
        categoryId2 = value.key;
        isShowWelcome.value = false;
        handleQueryEbook();
      }
      // isShowWelcome.value = value.key === 'welcome';
    };

    //initialization puts in onMounted
    onMounted(() => {
      handleQueryCategory();
      //handleQueryEbook();

    })

    return {
      ebooks,
      // ebooksContent: toRef(ebookA, "books"),
      // listData,
      pagination: {
        onChange: (page: any) => {
          console.log(page);
        },
        pageSize: 3,
      },
      // actions: [
      //   { type: 'StarOutlined', text: '156' },
      //   { type: 'LikeOutlined', text: '156' },
      //   { type: 'MessageOutlined', text: '2' },
      // ],

      handleClick,
      level1,

      isShowWelcome
    }
  }
});
